.App{
  text-align: center;
  width: 100%;
  height: 100vh;
  font-family: 'Nova Mono', monospace;
}


.App.dark {
  background: rgb(2, 0, 36);
  background: linear-gradient(
    332deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(12, 27, 43, 1) 35%,
    rgba(29, 62, 96, 1) 100%
  );
}


.App.light {
  background-color: white;
}